const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			list: [],
			page: '1',
			page_size: '10',
			boardId: '0',
			imgUrl: '',
			bank_id: '0',
			child_id: '0',
			vip: '',
			isShow: true,
			isShow1: false
		};
	},
	onShow(e) {
		// console.log(e)
		// this.board_id == e
	},
	onLoad(e) {
		console.log(e.id);
		console.log(e.bankId);
		this.imgUrl = helper.imgUrl;
		console.log(this.imgUrl);
		this.boardId = e.id || 0;

		this.bank_id = e.bankId || 0;
		this.child_id = e.childId || 0;
		this.getList();
		uni.setNavigationBarTitle({
			title: e.name
		});
	},
	onLaunch() {},
	methods: {
		getList() {
			uni.request({
				url: `${helper.requestUrl}/posts/board-posts`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data: {
					board_id: this.boardId,
					bank_id: this.bank_id,
					child_id: this.child_id,
					page_size: this.page_size,
					page: this.page
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						this.list = this.list.concat(res.data.data);
						if (res.data.data == 0) {
							uni.showToast({
								title: '暂无更多数据',
								icon: 'none'
							});
							this.isShow = true;
							console.log(this.isShow);
							this.isShow1 = false;
						} else {
							this.isShow = false;
							this.isShow1 = false;
						}
					} else if (res.data.status_code == 202) {
						this.vip = res.data.message;
						this.isShow1 = true;
						this.isShow = false;
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
				}
			});
		},
		// 跳转详情
		goDetail(e) {
			uni.navigateTo({
				url: `/pages/articleDetail?id=${e}`
			});
		},
		onReachBottom() {
			this.page++;
			this.getList();
		}
	}
};
